<template>
    <div class="addressWarp">
        <header>
            <router-link to="/home">
                <i class="el-icon-arrow-left"></i>
            </router-link>
            <div class="head_div1">编辑地址</div>
            <div class="head_div2" @click="bianBtn">{{showText}}</div>
        </header>
        <section class="address_section">
            <ul class="addresslist">
                <li v-for="(item,index) in userList" :key="item.id">
                    <div>
                        <p>{{item.address}}</p>
                        <p>{{item.phone}}</p>
                    </div>
                    <div class="deletesite" v-if="isShowCha" @click="chaBtn(item,index)">
                        <span><i class="el-icon-close"></i></span>
                    </div>
                </li>
            </ul>
            <router-link to="/add" style="text-decoration: none">
                <div class="addsite">
                    <span>新增地址</span>
                    <i class="el-icon-arrow-right"></i>
                </div>
            </router-link>
        </section>
    </div>
</template>

<script>
    export default {
        name: "Address",
        data(){
            return {
                isShow:false,
                showText:'编辑',//看左边是编辑还是完成
                isShowCha:false,//叉号
                userId:"",
                userList:[]
            }
        },
        methods: {
            bianBtn() {
                if (this.showText == "编辑") {
                    this.showText = "完成"
                    this.isShowCha = true

                } else if (this.showText == "完成") {
                    this.showText = "编辑"
                    this.isShowCha = false
                }

            },
            chaBtn(i,index) {
                console.log(i,index)
                  this.axios.delete("/v1/users/"+this.userId+ "/addresses/"+i.id).then(res=>{
                       console.log(res)
                      this.axios.get("/v1/users/"+this.userId+"/addresses").then(res=>{
                          console.log(res)
                          this.userList = res;
                      }).catch(err=>{})
                  }).catch(err=>{})
            }
        },
        created(){
            this.userId = localStorage.getItem("user_id")
            this.axios.get("/v1/users/"+this.userId+"/addresses").then(res=>{
                console.log(res);
                this.userList = res;

            }).catch(err=>{})
        }
    }
</script>

<style scoped>
    .addressWarp {
        padding-top: 1.95rem;
    }
    header {
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    .head_div1 {
        width: 50%;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
        color: #fff;
        text-align: center;
        font-size: .8rem;
        font-weight:700;
    }
    .head_div2{
        right: .4rem;
        font-size: .7rem;
        color: #fff;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }
    header>a {
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
        color: #fff;
    }

    .address_section{
        width: 100%;
        margin-top: .4rem;
        border-top: 1px solid #d9d9d9;
        border-bottom: 1px solid #d9d9d9;
    }
    .addresslist{
        background-color: #fff;
    }
    .addresslist li{
        background: #fff8c3;
        border-bottom: 1px solid #d9d9d9;
        padding: .4rem;
        display: flex;
        justify-content: space-between;
    }
    .addresslist li p {
        line-height: .9rem;
        font-size: .6rem;
        color: #333;
    }

    .deletesite{
        display: flex;
        align-items: center;
    }
    .deletesite span{
        display: block;
        font-size: .8rem;
        color: #999;
    }

    .addsite{
        margin-top: .4rem;
        background: #fff;
        padding: .2rem .4rem;
        border-top: 1px solid #d9d9d9;
        display: flex;
        justify-content: space-between;
    }
    .addsite span{
        display: block;
        font-size: .7rem;
        color: #333;
        line-height: 1.4rem;
    }
    .el-icon-arrow-right{
        font-size: .7rem;
        color: #333;
        line-height: 1.4rem;
        width: .66667rem;
        height: 1.4rem;
       color:  rgb(216, 216, 216);
    }

</style>